export class USEEOReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			form_setup_ui_dic: {},
			form_setup_data: {},

			save_form_setup_data: {}
		} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'USEEOReport';
		this.viewId = 'USEEOReport';
		this.context_menu_name = $.i18n._( 'US EEO' );
		this.navigation_label = $.i18n._( 'Saved Report' ) + ':';
		this.view_file = 'USEEOReportView.html';
		this.api = TTAPI.APIUSEEOReport;
		this.api_paystub = TTAPI.APIPayStubEntryAccount;
		this.authentication_api = TTAPI.APIAuthentication;
		this.include_form_setup = true;
		this.form_setup_data = {};
	}

	initOptions( callBack ) {
		var options = [
			{ option_name: 'page_orientation' },
			{ option_name: 'font_size' },
			{ option_name: 'auto_refresh' },
			{ option_name: 'chart_display_mode' },
			{ option_name: 'chart_type' },
			{ option_name: 'templates' },
			{ option_name: 'setup_fields' },
			{ option_name: 'export_type' },
			{ option_name: 'eeo1_report_type' }
		];

		this.initDropDownOptions( options, function( result ) {
			callBack( result ); // First to initialize drop down options, and then to initialize edit view UI.
		} );
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			groups: {
				form: {
					label: $.i18n._( 'Export' ), // Export is deliberate (rather than Form) due to export setup tab
					id: this.viewId + 'Form'
				}
			},
			exclude: [],
			include: [
				{
					label: $.i18n._( 'Export' ),
					id: 'export_export',
					group: 'editor',
					vue_icon: 'tticon tticon-file_upload_black_24dp',
					menu_align: 'right'
				},
				{
					label: $.i18n._( 'Save Setup' ),
					id: 'save_setup',
					group: 'form',
					vue_icon: 'tticon tticon-settings_black_24dp',
					menu_align: 'right',
					sort_order: 3000
				},
				{
					label: '', //Empty label. vue_icon is displayed instead of text.
					id: 'other_header',
					menu_align: 'right',
					action_group: 'other',
					action_group_header: true,
					vue_icon: 'tticon tticon-more_vert_black_24dp',
					sort_order: 4000
				},
				{
					label: $.i18n._( 'Create Custom Fields' ),
					id: 'generate_custom_fields',
					menu_align: 'right',
					action_group: 'other',
					permission_result: true,
					permission: null
				},
			]
		};

		return context_menu_model;
	}

	onContextMenuClick( context_btn, menu_name ) {
		var id;
		if ( Global.isSet( menu_name ) ) {
			id = menu_name;
		} else {

			if ( context_btn.disabled ) {
				return;
			}
		}

		if ( this.select_grid_last_row ) {
			this.export_grid.grid.jqGrid( 'saveRow', this.select_grid_last_row );
			this.select_grid_last_row = null;
		}

		var message_override = $.i18n._( 'Setup data for this report has not been configured yet. Please click on the Form Setup tab to do so now.' );

		switch ( id ) {
			case 'view':
				ProgressBar.showOverlay();
				this.onViewClick( null, false, message_override );
				break;
			case 'view_html':
				ProgressBar.showOverlay();
				this.onViewClick( 'html', false, message_override );
				break;
			case 'view_html_new_window':
				this.onViewClick( 'html', false, message_override );
				break;
			case 'export_excel':
				this.onViewExcelClick( message_override );
				break;
			case 'export_export':
				ProgressBar.showOverlay();
				this.onViewClick( 'efile', false, message_override );
				break;
			case 'generate_custom_fields':
				this.generateCustomFields();
				break;
			case 'cancel':
				this.onCancelClick();
				break;
			case 'save_existed_report': //All report view
				this.onSaveExistedReportClick();
				break;
			case 'save_new_report': //All report view
				this.onSaveNewReportClick();
				break;
			case 'save_setup': //All report view
				this.onSaveSetup( $.i18n._( 'Form setup' ) );
				break;
		}
		Global.triggerAnalyticsContextMenuClick( context_btn, menu_name );
	}

	generateCustomFields() {
		let $this = this;

		this.api.generateCustomFields( this.edit_view_ui_dic.export_type.getValue(), {
			onResult: function( result ) {
				//Clear CustomField cache since we just modified them.
				Global.refreshCustomFieldCache();
				//Clear export type codes to pull in the generated custom fields.
				Global.clearCache( 'export_type_codes' );

				//Trigger a refresh of the custom field tab with current export type or the first export type in the array.
				//This is to help show the new custom fields that were just created without having to refresh the page.
				$this.onFormSetupChange( $this.edit_view_ui_dic.export_type.getValue() ); //Cannot use $this.save_form_setup_data.export_type as it form setup may not be saved yet and we do not want to change the users current selection.

				TAlertManager.showAlert( $.i18n._( 'Custom fields created successfully.' ), $.i18n._( 'Create Custom Fields' ) );
			}
		} );
	}

	buildEditViewUI() {
		super.buildEditViewUI();

		var tab_3_label = this.edit_view.find( 'a[ref=tab_form_setup]' );
		tab_3_label.text( $.i18n._( 'Form Setup' ) );
	}

	buildFormSetupUI() {
		var $this = this;
		var tab3 = this.edit_view_tab.find( '#tab_form_setup' );
		var tab3_column1 = tab3.find( '.first-column' );
		this.edit_view_tabs[3] = [];
		this.edit_view_tabs[3].push( tab3_column1 );

		//Form setup
		var form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'export_type', set_empty: false } );
		form_item_input.setSourceData( $this.export_type_array );
		this.addEditFieldToColumn( $.i18n._( 'Format' ), form_item_input, tab3_column1, '' );

		form_item_input.bind( 'formItemChange', function( e, target ) {
			$this.onFormSetupChange( target.getValue() );
		} );
	}

	onFormSetupChange( export_type ) {
		var $this = this;

		this.removeCurrentFormSetupUI();
		this.form_setup_ui_dic = {};

		switch ( export_type ) {
			case 'eeo1':
				var tab3 = this.edit_view_tab.find( '#tab_form_setup' );
				var tab3_column1 = tab3.find( '.first-column' );
				this.edit_view_tabs[3] = [];
				this.edit_view_tabs[3].push( tab3_column1 );


				var field_name = 'form_setup_' + export_type + '_report_type';
				var form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
				form_item_input.TComboBox( { field: field_name, set_empty: false } );
				form_item_input.setSourceData( $this.eeo1_report_type_array );
				this.addEditFieldToColumn( $.i18n._( 'Type' ), form_item_input, tab3_column1, '', null, true );
				this.setWidgetVisible( [form_item_input] );
				this.form_setup_ui_dic[field_name] = this.edit_view_form_item_dic[field_name];
				delete this.edit_view_form_item_dic[field_name];

				if ( this.save_form_setup_data && this.save_form_setup_data.eeo1 ) {
					form_item_input.setValue( this.save_form_setup_data.eeo1.report_type );
				}

				//By that point, you will have already entered or updated all Company Information including Company Name, Address, EIN, NAICS, Certifying Official, Answers to Eligibility Questions, and Workforce Snapshot Pay Period used for the report.
				//Include full-time and part-time employees.
				$this.buildAdditionalCustomInputBox( export_type, 'company_id', $.i18n._( 'Company ID' ) ); //EEO-1: Field 1
				$this.buildAdditionalCustomInputBox( export_type, 'establishment_id', $.i18n._( 'Establishment ID' ) ); //EEO-1: Field 3
				$this.buildAdditionalCustomInputBox( export_type, 'establishment_uei', $.i18n._( 'Federal Contractor UEI' ) ); //EEO-1: Field 188 Unique Entity ID (UEI) for federal contractors.
				$this.buildAdditionalCustomInputBox( export_type, 'job_category', $.i18n._( 'Job Category' ) );
				$this.buildAdditionalCustomInputBox( export_type, 'ethnicity', $.i18n._( 'Race/Ethnicity' ) );
				break;
			case 'eeo4':
				$this.buildAdditionalCustomInputBox( export_type, 'function', $.i18n._( 'Function' ) );
				$this.buildAdditionalCustomInputBox( export_type, 'job_category', $.i18n._( 'Job Category' ) );
				$this.buildAdditionalCustomInputBox( export_type, 'ethnicity', $.i18n._( 'Race/Ethnicity' ) );
				$this.buildAdditionalCustomInputBox( export_type, 'employment_status', $.i18n._( 'Employment Status' ) ); //Full/Part Time

				$this.buildIncludeExcludeDropdown( export_type, 'regular_earning', 'pay_stub_entry_account', $.i18n._( 'Regular Earnings' ) );
				break;
			case 'eeo_ca':
			//case 'il_eeo': //TODO: Figure out what is needed for IL. -- Seems they accept Federal EEOC-1 data, with actual wages.
				$this.buildAdditionalCustomInputBox( export_type, 'establishment_id', $.i18n._( 'Establishment ID' ) ); //Branch or Legal Entity, as Address is needed.
				$this.buildAdditionalCustomInputBox( export_type, 'establishment_is_hq', $.i18n._( 'Establishment Headquarters' ) ); //Is HQ
				$this.buildAdditionalCustomInputBox( export_type, 'naics_code', $.i18n._( 'NAICS Code' ) ); //If legal entity, get it from there?
				$this.buildAdditionalCustomInputBox( export_type, 'major_activity', $.i18n._( 'Major Activity' ) ); //If legal entity, get it from there?

				$this.buildAdditionalCustomInputBox( export_type, 'job_category', $.i18n._( 'Job Category' ) );
				$this.buildAdditionalCustomInputBox( export_type, 'ethnicity', $.i18n._( 'Race/Ethnicity' ) );

				//How to differential part-time vs. full-time vs. newly hired employees?
				// $this.buildAdditionalCustomInputBox( export_type, 'job_category', $.i18n._( 'Full / Part-Time' ) );

				//Wages and Hours to be included need to defined.

				$this.buildIncludeExcludeDropdown( export_type, 'regular_earning', 'pay_stub_entry_account', $.i18n._( 'Regular Earnings' ) );
				break;
			default:
				this.edit_view_tab.find( '#tab_form_setup .first-column' ).append( '<div id="no-setup-data" style="font-weight: bold; text-align:center">'+ $.i18n._( 'No form setup required for this format.' ) +'</div>' );
				break;
		}
	}

	/**
	 * Overridden to allow stateful form_setup formats. This ensures your changes are put into memory..
	 *
	 * @param target
	 * @param doNotDoValidate
	 */
	onFormItemChange( target, doNotValidate ) {
		if ( target && target.getField && target.getField() == 'export_type' ) { // cannot read property getField of undefined
			var other = {};
			other.export_type = this.current_edit_record.export_type;
			other[other.export_type] = {};

			// this.save_form_setup_data[other.export_type] = this.getFormSetupData( other );
			this.form_setup_changed = true;
			return; //make room for the custom event above
		}
		super.onFormItemChange( target, doNotValidate );
	}

	/**
	 * Get the form setup data from the api
	 * @param res_Data
	 */
	setFormSetupData( res_data ) {
		if ( !res_data ) {
			this.show_empty_message = true;
		}

		this.save_form_setup_data = res_data;

		if ( res_data.export_type && this.edit_view_ui_dic.export_type ) {
			this.edit_view_ui_dic.export_type.setValue( res_data.export_type );
			this.onFormSetupChange( res_data.export_type );

			let key_prefix = `form_setup_${res_data.export_type}_`;

			let batch_get_real_data = this.processFormSetupDataAndAddToBatch( res_data, [
				{ data: _.get(res_data, [ res_data.export_type, 'regular_earning']), field_key: key_prefix + 'regular_earning', api: this.api_paystub, api_method: 'getPayStubEntryAccount' },
				{ data: _.get(res_data, [ res_data.export_type, 'regular_earning']), field_key: key_prefix + 'regular_earning', api: this.api_paystub, api_method: 'getPayStubEntryAccount' },
			] );

			this.getBatchedRealFormDataFromAPI( batch_get_real_data );
		} else if ( Array.isArray( this.export_type_array ) && this.export_type_array.length > 0 ) {
			this.onFormSetupChange( this.export_type_array[0]['value'] );
		}
	}

	buildAdditionalCustomInputBox( export_type, code, label ) {
		if ( !this.save_form_setup_data ) {
			this.save_form_setup_data = {};
		}

		if ( !this.save_form_setup_data[export_type] ) {
			this.save_form_setup_data[export_type] = {};
		}

		var $this = this;

		var tab3 = this.edit_view_tab.find( '#tab_form_setup' );
		var tab3_column1 = tab3.find( '.first-column' );
		this.edit_view_tabs[3] = [];
		this.edit_view_tabs[3].push( tab3_column1 );

		var field_name = 'form_setup_' + export_type + '_' + code;

		var form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: field_name } );

		var h_box = $( '<div class=\'h-box\'></div>' );
		var text_box = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		text_box.css( 'margin-left', '10px' );
		text_box.TTextInput( { field: field_name + '_text' } );
		h_box.append( form_item_input );
		h_box.append( text_box );

		this.addEditFieldToColumn( $.i18n._( label ), [form_item_input, text_box], tab3_column1, '', h_box, true );
		this.setWidgetVisible( [form_item_input, text_box] );
		form_item_input.bind( 'formItemChange', ( e, target ) => {
			if ( target.getValue() === 0 ) {
				if ( $this.save_form_setup_data[export_type][code] ) {
					text_box.setValue( $this.save_form_setup_data[export_type][code] );
				}
				text_box.css( 'display', 'inline' );
			} else {
				text_box.css( 'display', 'none' );
				text_box.setValue( '' );
			}
		} );

		this.api.getOptions( 'export_type_codes', {
			onResult: ( result ) => {
				var result_data = result.getResult();

				form_item_input.setSourceData( Global.buildRecordArray( result_data ) );

				if ( this.save_form_setup_data[export_type] && this.save_form_setup_data[export_type][code] ) {
					form_item_input.setValue( this.save_form_setup_data[export_type][code] );
				}
				form_item_input.trigger( 'formItemChange', [form_item_input, true] );
			}
		} );

		this.form_setup_ui_dic[field_name] = this.edit_view_form_item_dic[field_name];
		delete this.edit_view_form_item_dic[field_name];

		this.editFieldResize( 3 );
	}

	buildIncludeExcludeDropdown( export_type, prefix, suffix, label ) {
		var tab3 = this.edit_view_tab.find( '#tab_form_setup' );
		var tab3_column1 = tab3.find( '.first-column' );

		var field_name_include = 'form_setup_' + export_type + '_' + prefix + '_include_' + suffix;
		var field_name_exclude = 'form_setup_' + export_type + '_' + prefix + '_exclude_' + suffix;

		var v_box = $( '<div class=\'v-box\'></div>' );

		var $this = this;

		//Selection Type
		var form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );
		form_item_input.AComboBox( {
			api_class: TTAPI.APIPayStubEntryAccount,
			allow_multiple_selection: true,
			layout_name: 'global_PayStubAccount',
			show_search_inputs: true,
			set_empty: true,
			field: field_name_include
		} );

		var form_item = this.putInputToInsideFormItem( form_item_input, $.i18n._( 'Include' ) );

		v_box.append( form_item );
		v_box.append( '<div class=\'clear-both-div\'></div>' );

		//Selection
		var form_item_input_1 = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input_1.AComboBox( {
			api_class: TTAPI.APIPayStubEntryAccount,
			allow_multiple_selection: true,
			layout_name: 'global_PayStubAccount',
			show_search_inputs: true,
			set_empty: true,
			field: field_name_exclude
		} );

		form_item = this.putInputToInsideFormItem( form_item_input_1, $.i18n._( 'Exclude' ) );

		v_box.append( form_item );

		this.addEditFieldToColumn( label, [form_item_input, form_item_input_1], tab3_column1, '', v_box, false, true );
		this.setWidgetVisible( [form_item_input, form_item_input_1] );

		this.form_setup_ui_dic['form_setup_' + export_type +'_'+ prefix +'_'+ suffix] = v_box.parent().parent() //Reference on form change to remove v_box and include/exclude fields

		if ( this.save_form_setup_data[export_type][prefix] && this.save_form_setup_data[export_type][prefix]['include_' + suffix] ) {
			this.edit_view_ui_dic[field_name_include].setValue( this.save_form_setup_data[export_type][prefix]['include_' + suffix] );
		}
		if ( this.save_form_setup_data[export_type][prefix] && this.save_form_setup_data[export_type][prefix]['exclude_' + suffix] ) {
			this.edit_view_ui_dic[field_name_exclude].setValue( this.save_form_setup_data[export_type][prefix]['exclude_' + suffix] );
		}

		this.editFieldResize( 3 );
	}

	removeCurrentFormSetupUI() {
		for ( var key in this.form_setup_ui_dic ) {
			var html_item = this.form_setup_ui_dic[key];
			html_item.remove();
		}

		$( '#no-setup-data' ).remove();

		//Error: Unable to get property 'find' of undefined or null reference in /interface/html5/ line 1033
		if ( !this.edit_view_tab ) {
			return;
		}

		var tab3 = this.edit_view_tab.find( '#tab_form_setup' );
		var tab3_column1 = tab3.find( '.first-column' );
		var clear_both_div = tab3_column1.find( '.clear-both-div' );

		clear_both_div.remove();
	}

	/**
	 * Gets array of properly configured values for the form_setup setup form.
	 *
	 * @param field_list Array
	 * @returns {{}|*}
	 */
	getFormSetupFieldValues( export_type, field_list ) {
		var retarr = {};

		for ( var i = 0; i < field_list.length; i++ ) {
			if ( this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i]] && !this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i]].getValue() ) {
				retarr[field_list[i]] = this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i] + '_text'].getValue();
				retarr[field_list[i] + '_value'] = this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i] + '_text'].getValue();
			} else {
				if ( !this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i]] ) {
					retarr[field_list[i]] = '';
				} else {
					retarr[field_list[i]] = this.edit_view_ui_dic['form_setup_' + export_type + '_' + field_list[i]].getValue();
				}
			}
		}

		//Include/Exclude regular earning accounts
		if ( this.edit_view_ui_dic['form_setup_' + export_type + '_regular_earning_include_pay_stub_entry_account'] ) {
			retarr['regular_earning'] = {
				include_pay_stub_entry_account: this.edit_view_ui_dic['form_setup_' + export_type + '_regular_earning_include_pay_stub_entry_account'].getValue(),
				exclude_pay_stub_entry_account: this.edit_view_ui_dic['form_setup_' + export_type + '_regular_earning_exclude_pay_stub_entry_account'].getValue()
			};
		}

		return retarr;
	}

	getFormData( other, for_display ) {
		if ( !other || !other.export_type ) {
			return false;
		}

		other[other.export_type] = this.getFormSetupFieldValues( other.export_type, this.getFieldList( other.export_type ) );

		if ( !this.save_form_setup_data ) {
			this.save_form_setup_data = {};
		}
		this.save_form_setup_data[other.export_type] = other[other.export_type];
		this.save_form_setup_data['export_type'] = other.export_type;

		if ( for_display ) {
			for ( var key in this.save_form_setup_data ) {
				if ( key !== false && typeof ( this.save_form_setup_data[key] ) !== 'string' ) {
					this.save_form_setup_data[key] = this.convertFormSetupValues( this.save_form_setup_data[key] );
				}
			}
		}

		return this.save_form_setup_data;
	}

	getFieldList( export_type ) {
		var field_list = [];

		switch ( export_type ) {
			case 'eeo1':
				field_list = ['report_type', 'company_id', 'establishment_id', 'establishment_uei', 'naics_code', 'job_category', 'ethnicity' ];
				break;
			case 'eeo4':
				field_list = ['function', 'job_category', 'ethnicity', 'employment_status'];
				break;
			case 'eeo_ca':
			//case 'il_eeo':
				field_list = ['establishment_id',  'establishment_is_hq', 'naics_code', 'major_activity', 'job_category', 'ethnicity'];
				break;
		}

		return field_list;
	}

	/* jshint ignore:start */
	getFormSetupData( for_view ) {
		var other = {};
		other.export_type = this.edit_view_ui_dic.export_type.getValue();

		other[other.export_type] = {};

		other = this.getFormData( other, true );

		return other;
	}

	/**
	 * Backwards compatible function for custom data to be moved from the way the api stores it to the way the form needs it.
	 *
	 * the old custom field data was stored in obj[key]
	 * new custom field data is stored in obj[key+'_value']
	 *
	 * ie. obj[company_code] is now obj[company_code_value]
	 *
	 * @param data
	 * @returns {*}
	 */
	convertFormSetupValues( data ) {
		for ( var api_data_key in data ) {
			var form_data_key = api_data_key.substr( 0, api_data_key.indexOf( '_value' ) );
			if ( api_data_key.search( '_value' ) > 0 ) {
				data[form_data_key] = data[api_data_key];
			}
		}
		//conversion for lower form_setup grid data from old format
		if ( data.form_setup_columns && !data.columns && data.export_type != 0 && data.form_setup_columns[data.export_type] ) {
			data.columns = {};
			data.columns = data.form_setup_columns[data.export_type].columns;
		}

		return data;
	}

}